"use client";

import React, { useEffect, useState } from "react";
import { collection, getDocs, orderBy, query, limit } from "firebase/firestore";
import { firestore } from "@/lib/firebase";
import Link from "next/link";

interface LogEntry {
  id: string;
  timestamp: Date;
  command: string;
  response: string;
  type: string;
}

export default function AssistantLogsPage() {
  const [logs, setLogs] = useState<LogEntry[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string>("");

  useEffect(() => {
    async function fetchLogs() {
      try {
        setLoading(true);
        const logsCollection = collection(firestore, "assistantLogs");
        const q = query(logsCollection, orderBy("timestamp", "desc"), limit(100));
        const snapshot = await getDocs(q);
        
        const logsData = snapshot.docs.map((doc) => {
          const data = doc.data();
          return {
            id: doc.id,
            timestamp: data.timestamp?.toDate ? data.timestamp.toDate() : new Date(data.timestamp),
            command: data.command || "",
            response: data.response || "",
            type: data.type || "unknown",
          };
        });
        
        setLogs(logsData);
      } catch (err: any) {
        console.error("Error fetching logs:", err);
        setError("Failed to load logs. Please check your Firebase configuration.");
      } finally {
        setLoading(false);
      }
    }

    fetchLogs();
  }, []);

  const getTypeColor = (type: string) => {
    switch (type.toLowerCase()) {
      case "command":
        return "bg-blue-100 text-blue-800 border-blue-200";
      case "error":
        return "bg-red-100 text-red-800 border-red-200";
      case "info":
        return "bg-green-100 text-green-800 border-green-200";
      default:
        return "bg-gray-100 text-gray-800 border-gray-200";
    }
  };

  const formatTimestamp = (timestamp: Date) => {
    return timestamp.toLocaleString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });
  };

  return (
    <div className="min-h-screen bg-background text-foreground">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <div className="flex items-center justify-between mb-4">
            <h1 className="text-3xl font-bold">Assistant Logs</h1>
            <Link 
              href="/" 
              className="px-4 py-2 bg-secondary text-secondary-foreground rounded-lg hover:bg-secondary/80 transition-colors"
            >
              Back to Home
            </Link>
          </div>
          <p className="text-muted-foreground">
            View all interactions with your Personal AI Assistant. Logs are automatically saved with timestamps.
          </p>
        </div>

        {/* Loading State */}
        {loading && (
          <div className="flex items-center justify-center py-12">
            <div className="text-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
              <p className="text-muted-foreground">Loading logs...</p>
            </div>
          </div>
        )}

        {/* Error State */}
        {error && (
          <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-6 mb-6">
            <h3 className="font-semibold text-destructive mb-2">Error Loading Logs</h3>
            <p className="text-destructive/80">{error}</p>
            <div className="mt-4 text-sm text-muted-foreground">
              <p>Make sure you have:</p>
              <ul className="list-disc list-inside mt-2 space-y-1">
                <li>Configured Firebase environment variables in .env.local</li>
                <li>Set up Firestore database with proper permissions</li>
                <li>Run the Python assistant to generate some logs</li>
              </ul>
            </div>
          </div>
        )}

        {/* Logs Display */}
        {!loading && !error && (
          <>
            {logs.length === 0 ? (
              <div className="text-center py-12">
                <div className="bg-muted/50 rounded-lg p-8 max-w-md mx-auto">
                  <h3 className="text-lg font-semibold mb-2">No Logs Found</h3>
                  <p className="text-muted-foreground mb-4">
                    Start using your Personal AI Assistant to see logs here.
                  </p>
                  <div className="text-sm text-muted-foreground">
                    <p>To generate logs:</p>
                    <ol className="list-decimal list-inside mt-2 space-y-1">
                      <li>Install Python dependencies</li>
                      <li>Configure Firebase credentials</li>
                      <li>Run: <code className="bg-background px-2 py-1 rounded">python assistant/assistant.py</code></li>
                      <li>Start talking to your assistant</li>
                    </ol>
                  </div>
                </div>
              </div>
            ) : (
              <>
                {/* Stats */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
                  <div className="bg-card border border-border rounded-lg p-4">
                    <h3 className="text-sm font-medium text-muted-foreground">Total Logs</h3>
                    <p className="text-2xl font-bold">{logs.length}</p>
                  </div>
                  <div className="bg-card border border-border rounded-lg p-4">
                    <h3 className="text-sm font-medium text-muted-foreground">Commands</h3>
                    <p className="text-2xl font-bold">
                      {logs.filter(log => log.type === "command").length}
                    </p>
                  </div>
                  <div className="bg-card border border-border rounded-lg p-4">
                    <h3 className="text-sm font-medium text-muted-foreground">Errors</h3>
                    <p className="text-2xl font-bold">
                      {logs.filter(log => log.type === "error").length}
                    </p>
                  </div>
                </div>

                {/* Logs Table */}
                <div className="bg-card border border-border rounded-lg overflow-hidden">
                  <div className="overflow-x-auto">
                    <table className="w-full">
                      <thead className="bg-muted/50">
                        <tr>
                          <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                            Timestamp
                          </th>
                          <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                            Type
                          </th>
                          <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                            Command
                          </th>
                          <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                            Response
                          </th>
                        </tr>
                      </thead>
                      <tbody className="divide-y divide-border">
                        {logs.map((log) => (
                          <tr key={log.id} className="hover:bg-muted/30 transition-colors">
                            <td className="px-4 py-3 text-sm text-muted-foreground whitespace-nowrap">
                              {formatTimestamp(log.timestamp)}
                            </td>
                            <td className="px-4 py-3">
                              <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium border ${getTypeColor(log.type)}`}>
                                {log.type}
                              </span>
                            </td>
                            <td className="px-4 py-3 text-sm max-w-xs">
                              <div className="truncate" title={log.command}>
                                {log.command}
                              </div>
                            </td>
                            <td className="px-4 py-3 text-sm max-w-md">
                              <div className="truncate" title={log.response}>
                                {log.response}
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>

                {/* Footer */}
                <div className="mt-6 text-center text-sm text-muted-foreground">
                  <p>Showing the latest {logs.length} log entries</p>
                </div>
              </>
            )}
          </>
        )}
      </div>
    </div>
  );
}
